body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


#welcome {
  width: 50%;
  text-align: left;
  margin: 0 auto;
}

#welcome button {
  padding: 1em;
  font-size: 1em;
  cursor: pointer;
  background: #d9e8f5;
}

.App-link {
  color: #61dafb;
}

#machines {
  list-style: none;
}

#machines li {
  float: left;
  width: 32%;
}

#machines h2 {
  text-align: center;
}

#machines img {
  width: 250px;
  display: block;
  margin: 0 auto;
}

#animate {
  display: block;
  z-index: 10;
  width: 300px;
  position: absolute;
  left: 31em;
  top: 10em;
  background:#fff57f9e;
}

.blur-filter {
  -webkit-filter: blur(2px);
  -moz-filter: blur(2px);
  -o-filter: blur(2px);
  -ms-filter: blur(2px);
  filter: blur(2px);
}

.message {
  text-align: center;
}

#gui {
  width: 70%;
  margin: 0 auto;
}

.advice {
  background: #fbef61;
}

#animate img {
  height: 200px;
  width: 200px;
  display: block;
  margin: 0 auto;
}

#animate button {
  width: 100px;
  display: block;
  margin: 0 auto;
  padding: 12px;
  font-size: 1em;
  background: #bde3ed;
  cursor: pointer;
}

#answers button {
  width: 100px;
  display: block;
  padding: 12px;
  font-size: 1em;
  background: #bde3ed;
  cursor: pointer;
}

#answers label, #answers span {
  display: block;
  margin-bottom: 10px;
}

#answers select {
  width: 100px;
  font-size: 1em;
}

#answers textarea {
  font-size: 1em;
  width: 350px;
  height: 50px;
}
